import { Button } from '@material-ui/core';
import React from 'react'
import {useState} from 'react'
import ErrorMessage from '../Error/ErrorMessage';
import './Questions.css';
import { useNavigate } from 'react-router';

export default function Questions({currentQuestion,setcurrentQuestion,question,option,score,setScore,correct}) {
    const [select, setselect] = useState();
    const [iserror, setiserror] = useState(false);
    const handleSelect = (i) =>{
     if(select === i && select===correct){
         return "selc";
     }else if(select===i && select!==correct){
         return "wrong";
     }else if(i === correct){
         return "selc";
     }
    };
    const handleCheck = (i) =>{
        setselect(i);
        if(i === correct) setScore(score+1);
        setiserror(false);
    }
    const handleQuit=()=>{
     
    };
    const navigate = useNavigate()
    const handleNext=()=>{
        if(currentQuestion > 8){
           navigate("/result")
        }else if(select){
            setcurrentQuestion(currentQuestion + 1);
            setselect();
        }else{
            setiserror("please select the option first")
        }
    }
    return (
        <div className="quest">
            <h1>QUESTION{currentQuestion + 1}</h1>
            <div className="newquestion">
                <h2>{question[currentQuestion].question}</h2>
                <div className="optionss">
                    {iserror && <ErrorMessage>{iserror}</ErrorMessage>}
                    {option &&
                    option.map((i)=>{
                        return(
                      <button
                      onClick={()=>handleCheck(i)}
                      className={`singleOption ${select && handleSelect(i)}`}
                      key={i}
                      disabled={select}>{i}</button>
                     ) })}
                </div>
                <div className="controls">
                    <Button
                    variant="contained"
                    color="secondary"
                    size="large"
                    style={{width:185}}
                    href="/"
                    onClick={handleQuit}
                    >QUIT</Button>
                    <Button
                    variant="contained"
                    color="primary"
                    size="large"
                    style={{width:185}}
                    onClick={handleNext}
                    >
                        NEXT QUESTION
                    </Button>

                </div>
            </div>

         </div>   
    )
}
