import { Button, MenuItem, TextField } from '@material-ui/core'
import React from 'react'
import './Home.css';
import Categories from '../Data/Categories';
import {useState} from 'react';
import {useNavigate} from 'react-router';
import ErrorMessage from '../Error/ErrorMessage'

export default function Home({name,setName,fetchQuestions}) {
    const [isLanguage, setIsLanguage] = useState("");
    const [error,setError] = useState(false)
    const navigate= useNavigate();
    const handleSubmit = () =>{
        if(!isLanguage||!name){
            setError(true);
            return;

        }else{
            setError(false);
            fetchQuestions(isLanguage);
            navigate("./quiz")
        }

    }
    return (
        <div className='content'>
           <div className="settings">
               <span style={{fontSize:30}}>STUDENT DETAILS</span>
           </div>
           <img src="/undraw_exams_g-4-ow.svg" className="banner" alt="classroom"/>
           
           <div className="studentdetail">
               {error&&<ErrorMessage>please fill all the details</ErrorMessage>}
               <TextField label="studentname" variant="outlined" onChange={(e)=>setName(e.target.value)}/>
               <TextField select label="select Categorie" variant="outlined" onChange={(e)=>setIsLanguage(e.target.value)} value={isLanguage}>
                   {
                       Categories.map((cat)=>{
                          return( 
                        <MenuItem key={cat.category} value={cat.value}>
                            {cat.category}
                        </MenuItem>
                       )})
                   }
               </TextField>
               <Button variant="contained" color="green" size="large" onClick={handleSubmit}>START TEST</Button>
           </div>
        </div>
    )
}
