
import './App.css';
import { BrowserRouter,Route,Routes } from 'react-router-dom';
import Header from './mycomponent/Header';
import Footer from './mycomponent/Footer';

import Home from './pages/Home';
import Question from './pages/Question';
import Result from './pages/Result';
import {useState} from 'react';
import axios from 'axios';


function App() {
  const [name, setName] = useState("");
  const [question, setquestion] = useState("");
  const [score, setScore] = useState(0);
  const fetchQuestions = async(category="", difficulty="") =>{
    const{data} = await axios.get(`https://opentdb.com/api.php?amount=10${
      category && `&category=${category}`
    }${difficulty && `&difficulty=${difficulty}`}&type=multiple`);
    setquestion(data.results);
    
  }
  return (
    <BrowserRouter>
    <div className ="container">
     <Header/>
     <Routes>
       <Route  path = '/examapp' element={<Home name={name} setName={setName} fetchQuestions={fetchQuestions}/>}/>
        
       <Route path = '/quiz'element={<Question name={name} question={question} score={score} setScore={setScore} setquestion={setquestion}/>} />
       
       <Route path = '/result' element={<Result name={name} score={score}/>}/>
        
     </Routes>
    </div>
    <Footer/>
    </BrowserRouter>
  );
}

export default App;
