import React from 'react';
import {useEffect,useState} from 'react';
import { CircularProgress } from '@material-ui/core';
import './Question.css';
import Questions from '../questions/Questions';


export default function Question({name,question,setquestion,score,setScore}) {
    const [option, setoption] = useState();
    const [currentQuestion, setcurrentQuestion] = useState(0);
    useEffect(() => {
     
        setoption(question && handleSuffle([question[currentQuestion]?.correct_answer,
            ...question[currentQuestion]?.incorrect_answers]))
    }, [currentQuestion,question]);
    const handleSuffle = (options) =>{
      return options.sort(()=>Math.random() - 0.5)
    };
    return (
        <div className="quiz">
            <span className="subTitle">WELCOME {name}</span>
            {question?(
            <>
            <div className="quizInfo">
                <span>{question[currentQuestion].category}</span>
                <span>score:{score}</span>
            </div>
            <Questions
            currentQuestion={currentQuestion}
            setcurrentQuestion={setcurrentQuestion}
            question={question}
            option={option}
            correct={question[currentQuestion]?.correct_answer}
            score={score}
            setScore={setScore}
            />
            </>):(
                <CircularProgress
                style={{margin:100}}
                color="inherit"
                size={150}
                thickness={1}
                />
            )}
        </div>
    )
}
