import React from 'react'

export default function ErrorMessage({children}) {
    return (
        <div
            style={{
                width:"100%",
                padding:10,
                marginBottom:10,
                borderRadius:4,
                backgroundColor:"red",
                testAlign:"center",
                color:"white",
                textTransform:"capitalization",
            }}>
                {children}
        </div>
    )
}
